* {
  font-size: 10px;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.mainDiv {
  width: 100%;
}

.bgImgStyles {
  display: block;
  background: rgba(0, 194, 197, 1);
  background-image: url(//d9hhrg4mnvzow.cloudfront.net/templates.unbounce.com/multor/abbcf2d7-multor-heroshot_11hc0nw00000000000001o.jpg);
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  border-style: none;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 0px;
  border-radius: 0px;
  width: 100%;
  height: 100vh;
  padding: 6rem 20rem;
}

.secondSec {
  display: block;
  background: rgba(255, 255, 255, 1);
  border-style: none;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 0px;
  border-radius: 0px;
  width: 100%;
  height: 631px;
  position: relative;
}

.formDiv {
  z-index: 32;
  width: 500px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 3rem;
  align-items: center;
  background: rgba(21, 47, 46, 1);
  border-style: none;
  border-radius: 0px;
  right: 10rem;
  top: -14vh;
  padding: 6rem;
  position: absolute;
}

.formDiv > span {
  color: white;
}

.formHeading {
  font-size: 4rem;
  text-align: center;
}

.formSubtext {
  font-size: 1.3rem;
  text-align: center;
  padding: 0 2rem;
}

.thirdSec {
  display: flex;
  padding: 10rem 20rem;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: rgba(232, 251, 251, 1);
  border-style: none;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 0px;
  border-radius: 0px;
  width: 100%;
  margin-top: 20rem;
  gap: 10rem;
  position: relative;
}

.videoStyles {
  height: 528.75px;
  width: 940px;
  clip: rect(0px, 0px, 0px, 0px);
  border-width: 0px;
  border-style: solid;
  border-color: rgb(0, 0, 0);
  box-sizing: content-box;
}

.sixthSec {
  display: flex;
  width: 100%;
  flex-wrap: wrap;
  justify-content: space-between;
  column-gap: 1rem;
  padding: 10rem 20rem;
}

footer {
  display: block;
  background: rgba(21, 47, 46, 1);
  border-style: none;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 0px;
  border-radius: 0px;
  width: 100%;
  height: 135px;
  position: relative;
}

.heading-text {
  font-size: 10em;
}

.header-text {
  width: 70%;
  font-weight: 900;
  margin: 10rem 0;
  display: flex;
  flex-direction: column;
  gap: 6rem;
}

.helper-text {
  font-size: 3rem;
  font-weight: 300;
  letter-spacing: 1px;
}

.parent-form {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 3rem;
}

.input-fields {
  width: 100%;
  padding: 2rem;
  border-radius: 8px;
  outline: none;
  border-width: 0;
}

.submit-button {
  padding: 2rem 2rem;
  background-color: tomato;
  color: white;
  font-weight: 500;
  font-size: 1.6rem;
  text-align: center;
}

.benefit-card {
  width: fit-content;
  display: flex;
}

.benefit-text {
  display: flex;
  flex-direction: column;
  padding: 0.5rem 3rem;
  gap: 2rem;
  width: fit-content;
}

.benefit-heading {
  font-size: 2.5rem;
  width: fit-content;
  font-weight: 800;
}

.descStyles {
  font-size: 1.75rem;
  line-height: 3rem;
  letter-spacing: 1px;
}

.benefit-cards-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 80%;
  gap: 4rem;
  margin-top: 15rem;
  max-width: 500px;
}

.third-heading {
  font-size: 6rem;
}

.third-subtext {
  font-size: 2rem;
}

.third-text-wrapper {
  text-align: center;
  width: 50%;
  display: flex;
  flex-direction: column;
  gap: 4rem;
  max-width: 1000px;
}

.forthSec {
  display: flex;
  padding: 10rem;
  gap: 10rem;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 10rem 20rem;
}

.forth-cards {
  display: flex;
  width: 100%;
  gap: 2rem;
}

.review-text {
  font-size: 3rem;
  margin-top: 1rem;
}

.forth-card {
  width: 48%;
  display: flex;
  flex-direction: column;
  padding: 4rem;
}

.profile-section {
  display: flex;
}

.name-section {
  padding: 2rem;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.forth-heading {
  font-size: 1.2rem;
  letter-spacing: 2px;
  color: rgb(0, 145, 145);
  font-weight: 700;
}

.card {
  width: 25rem;
  display: flex;
  flex-direction: column;
  gap: 3rem;
}

.card-title {
  font-size: 1.5rem;
}

.card-desc {
  font-size: 1.2rem;
}

.about-section {
  padding: 10rem 20rem;
}

.about-desc {
  padding: 5rem;
  display: flex;
  flex-direction: column;
  gap: 2rem;
  width: 50%;
  padding: 10rem;
}

.about-image {
  width: 50%;
  display: flex;
  justify-content: flex-end;
}

.lheader-text {
  font-size: 3.5rem;
}

.lhelper-text {
  font-size: 1.6rem;
}

.modal-dark-background {
  background-color: rgba(0, 0, 0, 0.6);
  position: fixed;
  top: 0px;
  right: 0px;
  left: 0px;
  bottom: 0px;
  z-index: 100;
  display: flex;
  justify-content: center;
  align-items: center;
}

.file-downloading-modal {
  background-color: white;
  width: 400px;
  padding: 20px;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  align-items: center;
  border-radius: 10px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
}

.file-downloading-modal h2 {
  font-size: 2rem;
  font-weight: bold;
}

.file-downloading-modal p {
  font-size: 1.6rem;
  text-align: center;
}

.form-button {
  padding: 1rem 2rem;
  font-size: 1.6rem;
  background-color: tomato;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}
footer {
  display: flex;
  background: rgba(21, 47, 46, 1);
  border-style: none;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 0px;
  border-radius: 0px;
  width: 100%;
  position: relative;
  padding: 8rem 2rem;
  color: rgb(226, 226, 226, 0.75);
  justify-content: space-between;
  align-items: center;
}

footer > img {
  width: 15rem;
}

footer > div {
}

/* Media Queries */
@media (max-width: 1500px) {
  .bgImgStyles {
    padding: 4rem 15rem;
  }

  .formDiv {
    width: 400px;
    padding: 4rem;
    right: 5rem;
  }

  .thirdSec {
    padding: 8rem 15rem;
  }

  .videoStyles {
    height: 420px;
    width: 750px;
  }

  .sixthSec {
    padding: 8rem 15rem;
  }

  .forthSec {
    padding: 8rem;
  }

  .about-section {
    padding: 8rem 15rem;
  }
}

@media (max-width: 1200px) {
  .bgImgStyles {
    padding: 4rem 10rem;
  }

  .formDiv {
    width: 300px;
    padding: 3rem;
    right: 3rem;
  }

  .thirdSec {
    padding: 6rem 10rem;
  }

  .videoStyles {
    height: 370px;
    width: 650px;
  }

  .sixthSec {
    padding: 6rem 10rem;
  }

  .forthSec {
    padding: 6rem;
  }

  .about-section {
    padding: 6rem 10rem;
  }
}

@media (max-width: 992px) {
  .bgImgStyles {
    padding: 4rem 5rem;
  }

  .formDiv {
    width: 100%;
    padding: 3rem;
    right: 0;
    top: auto;
    transform: translateY(0%);
  }

  .secondSec {
    flex-direction: column;
  }

  .formHeading {
    font-size: 3.5rem;
  }

  .formSubtext {
    font-size: 1.2rem;
  }

  .thirdSec {
    padding: 6rem 5rem;
  }

  .videoStyles {
    height: 300px;
    width: 500px;
  }

  .forthSec {
    padding: 6rem;
  }

  .sixthSec {
    padding: 4rem 5rem;
  }

  .about-section {
    padding: 4rem 5rem;
  }
}

@media (max-width: 768px) {
  .header-text {
    width: 100%;
  }

  .heading-text {
    font-size: 8em;
  }

  .helper-text {
    font-size: 2.5rem;
  }

  .benefit-cards-container {
    width: 100%;
    max-width: none;
  }

  .forth-card {
    width: 100%;
  }

  .card {
    width: 100%;
  }
}

@media (max-width: 576px) {
  .bgImgStyles {
    padding: 4rem 2rem;
  }

  .formDiv {
    padding: 2rem;
  }

  .formHeading {
    font-size: 3rem;
  }

  .formSubtext {
    font-size: 1rem;
  }

  .thirdSec {
    padding: 4rem 2rem;
  }

  .videoStyles {
    height: 200px;
    width: 350px;
  }

  .forthSec {
    padding: 4rem 2rem;
  }

  .sixthSec {
    padding: 2rem;
  }

  .about-section {
    padding: 2rem;
  }

  .about-image img {
    width: 100%;
    height: auto;
  }
}
